	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/shottts-dribbble-for-the-blackberry-playbook/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/portfolio-2018/static/shottts-logo-d3ced20d55f37d4b01a1d01daed87de6-41a8d.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 55.99999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"shottts logo\"\n        title=\"\"\n        src=\"/portfolio-2018/static/shottts-logo-d3ced20d55f37d4b01a1d01daed87de6-41a8d.png\"\n        srcset=\"/portfolio-2018/static/shottts-logo-d3ced20d55f37d4b01a1d01daed87de6-bf474.png 158w,\n/portfolio-2018/static/shottts-logo-d3ced20d55f37d4b01a1d01daed87de6-d5dd3.png 315w,\n/portfolio-2018/static/shottts-logo-d3ced20d55f37d4b01a1d01daed87de6-41a8d.png 500w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p><a href=\"http://appworld.blackberry.com/webstore/content/30821\" title=\"View the Shottts application on the BlackBerry App World website.\">Shottts</a> is an AIR application developed for the <a href=\"http://uk.blackberry.com/playbook-tablet/\" title=\"Read more about the Playbook Tablet\">BlackBerry Playbook Tablet</a>, developed by Mark Starling, of <a href=\"http://markstar.co.uk\" title=\"Open http://markstar.co.uk in a new window.\">http://markstar.co.uk</a> fame.</p>\n<p>Shottts loads and displays a selection of the most recent updates from <a href=\"http://dribbble.com/shots/popular/\" title=\"Open the most popular shots from Dribbble in a new window.\">Dribbble</a>, and displays them in a nice, touch screen interface. I happen to know that earlier desktop versions of the Shottts application were kicking about privately before BlackBerry launched their submit-an-app-get-a-free-Playbook contest, so its nice to see the app finally go live!</p>\n<p>And once those Playbook devices go on sale over in the UK, and Mark can actually get his mitts on a physical device, I'm sure we'll see more application updates. <a href=\"http://markstar.co.uk/blog/2011/flashplatform/actionscript/shottts-is-now-available-in-the-blackberry-app-store/\" title=\"Read more about the release of Shottts at markstar.co.uk\">You can read more from Mark about Shottts in his announcement blog post.</a></p>\n<p>If you've got a Playbook, I'd suggest you <a href=\"http://appworld.blackberry.com/webstore/content/30821\" title=\"Download Shottts from the BlackBerry App World store.\">check out Shottts</a>!</p>","frontmatter":{"title":"Shottts - Dribbble for the BlackBerry Playbook","date":"May 09, 2011","tags":["air","blackberry","dribbble","playbook","shottts"],"url":null}}},"pathContext":{"prev":{"frontmatter":{"path":"/blog/setting-up-asdoc-in-flex-builder/","tags":["actionscript","flex","asdoc"],"title":"Setting up ASDoc in Flex Builder","image":null,"date":"2009-03-16T09:46:55.000Z"}},"next":{"frontmatter":{"path":"/blog/sending-authentication-headers-in-flex/","tags":["flex"],"title":"Sending Authentication Headers in Flex","image":null,"date":"2009-02-10T09:30:28.000Z"}}}}

